// Popup
.mfp {
  position: relative;
  max-width: 1330px;
  min-height: 695px;
  text-align: left;
  padding: 30px 54px 1px 56px;
  margin: 2.5rem auto;
  background: #fff;
  @include md {
    max-width: 450px; }

  &__title {
    color: $black;
    font-size: 46px;
    font-weight: 700;
    line-height: 125%;
    letter-spacing: 0.92px;
    margin-bottom: 16px; }

  &__desc {
    color: $gray;
    font-size: 26px;
    font-weight: 400;
    line-height: 125%;
    letter-spacing: 0.52px;
    margin-bottom: 26px;
    max-width: 820px; }

  &__form {
    display: flex;
    justify-content: space-between;
    column-gap: 40px; }

  &__form-col {
    width: 100%; }

  &__form-submit {
    padding: 10px 48px;
    display: block;
    margin-left: auto; }

  .mfp-close {
    top: 40px;
    right: 40px;
    opacity: 1;
    transition: all .2s ease;
    &:focus {
      outline: none; }
    &:active {
      transform: translateY(1px); }
    img {
      position: relative;
      z-index: -1; } } }

.form-result {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: #fff;
  opacity: 0;
  padding: 50px;
  visibility: hidden;
  transition: all .3s ease;
  z-index: 1;
  .mess {
    &__title {
      font-size: 42px;
      line-height: 47px;
      font-weight: 600;
      margin-bottom: 10px;
      opacity: 0;
      transform: translateY(50px);
      transition: all .6s ease;
      @include sm {
        font-size: 30px; }
      @include xsm {
        font-size: 25px;
        margin-bottom: 5px; } }

    &__desc {
      font-size: 22px;
      line-height: 27px;
      opacity: 0;
      transform: translateY(50px);
      transition: all .6s ease;
      @include xsm {
        font-size: 16px; } } }

  &--success {
    opacity: 1;
    visibility: visible;
    .mess__title,
    .mess__desc {
      opacity: 1;
      transform: translateY(0);
      transition-delay: .1s; }
    .mess__title {
      transition-delay: .2s; }
    .mess__desc {
      transition-delay: .3s; } } }

.log {
  display: flex;
  align-items: center;
  column-gap: 12px;

  &-desc {
    color: $gray;
    font-size: 18px;
    font-weight: 400;
    line-height: 125%;
    letter-spacing: 0.36px; } }

.promocode-wrap {
  position: relative; }

.promocode-input {
  padding-right: 210px !important; }

.promocode-btn {
  position: absolute;
  top: 5px;
  right: 5px;
  padding: 10px 47px; }

.password-wrap {
  position: relative; }

.password-input {}

.show-btn {
  position: absolute;
  top: 10px;
  right: 18px;
  cursor: pointer; }
