.cross {
  width: 42px;
  height: 42px;
  border: 2px solid #2A84EE;
  border-radius: 50%;
  position: relative;
  transition: all 0.3s ease; }

.cross span:first-child {
  display: inline-block;
  width: 16px;
  height: 2.67px;
  background: #2A84EE;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 150px;
  transition: all 0.3s ease; }

.cross span:last-child {
  display: inline-block;
  width: 16px;
  height: 2.67px;
  background: #2A84EE;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(90deg);
  border-radius: 150px;
  transition: all 0.3s ease; }
