.services {
  padding-top: 87px;
  padding-bottom: 119px;

  &__title {
    margin-bottom: 39px; }

  &__content {}

  &__tab {} }

.tab {
  background: #E8F2FC;
  box-shadow: 0px 10px 50px 0px rgba(46, 184, 151, 0.10);

  &-nav-wrap {}

  &-nav {

    &__wrap {
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      grid-template-rows: 111px; }

    &__link {
      font-size: 26px;
      font-weight: 400;
      line-height: 115%;
      letter-spacing: 0.52px;
      color: $green;
      text-align: center;
      padding: 30px 13px 5px 13px;
      &:hover {
        color: $black; } }

    &__link--active {
      color: $black;
      background: #F6FAFF; } }

  &-content-wrap {}

  &-content {
    background: #F6FAFF;
    padding: 51px 50px 60px 39px; }

  &__content-item {} }

.item-content {
  display: flex;
  justify-content: space-between;

  &__box {}

  &__title {
    font-size: 30px;
    font-weight: 700;
    line-height: 125%;
    letter-spacing: 0.6px;
    color: $black;
    margin-bottom: 23px; }

  &__list {
    display: flex;
    flex-direction: column;
    row-gap: 19px;
    margin-bottom: 38px;
    padding-left: 4px; }

  &__img img {
    border-radius: 20px; }

  &__item {
    font-size: 22px;
    font-weight: 400;
    line-height: 115%;
    letter-spacing: 0.44px;
    color: $gray;
    display: flex;
    align-items: center; }

  &__item span {
    display: inline-block;
    width: 6px;
    height: 6px;
    background: $accent-gt;
    border-radius: 50%;
    margin-right: 13px; }

  &__btn {
    padding: 13px 36px; } }
