.choose {
  padding-top: 23px;
  padding-bottom: 140px;

  &__title {
    margin-bottom: 41px; }

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px; }

  &__item {
    border-radius: 10px;
    background: $bg;
    padding: 38px 30px 38px 40px; }

  &__item-header {
    display: flex;
    column-gap: 18px;
    margin-bottom: 9px; }

  &__item-icon {}

  &__item-title {
    font-size: 30px;
    font-weight: 700;
    line-height: 125%;
    letter-spacing: 0.6px;
    color: $black; }

  &__item-text {
    font-size: 22px;
    font-weight: 400;
    line-height: 125%;
    letter-spacing: 0.44px;
    color: #707070; } }
